import React, { Component } from 'react';

/*
                  - Show cartola market closure
                      # Status: 2 = close
                      https://api.cartolafc.globo.com/mercado/status
                  - Most scaled players in the round and the partial score
                      https://api.cartolafc.globo.com/mercado/destaques
                      and
                      https://api.cartolafc.globo.com/atletas/pontuados
                  - Round Games
                    https://api.cartolafc.globo.com/partidas
              */
class Home extends Component {
	constructor(props) {
		super(props);
		
		this.state = { 
			info_market: {},
			players: [],
			games: []
		};

		this.api_req = this.api_req.bind(this);
	}

	api_req() {
		fetch('/api/mensagem')
	  .then(function(response) {
	    return response;
	  }).then(function(json) {
	    console.log('parsed json', json)
	  }).catch(function(ex) {
	    console.log('parsing failed', ex)
	  })
	}

	componentDidMount() {
		this.api_req();
  }

	render() {
    return (
      <div>
      	<h3> HOMEEEEEEEEE</h3>
      </div>
    );
  }
}

export default Home;


